.review {
  width: 100%;

  background-color: white;
}

.review__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 91%;
  max-width: 320px;
  margin: 0 auto;
  padding-bottom: 35px;
}

.review-slider {
  .list-style-none;
  display: flex;
  flex-direction: column;
  align-items: center;

  margin: 35px 0 25px;
}

.review-slider__item {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 98%;

  &:not(&--active) {
    display: none;
  }
}

.review-slider__item-name {
  margin: 0 0 15px 0;
  padding: 0;

  font-size: 18px;
  line-height: 30px;
  font-weight: bold;
}

.review-slider__item-info {
  display: none;
}

.review-slider__item-text {
  width: 280px;
  margin: 0;
  padding: 0;

  font-size: 14px;
  line-height: 30px;
  font-weight: 300;
  text-align: center;
}

.review__slider-controls {
  display: flex;

  margin: 0 0 130px;
}

.slider-controls__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 65px;
}

.slider-controls__wrapper label {
  position: relative;

  width: 6px;
  height: 6px;

  font-size: 0;

  background-color: grey;
  border-radius: 50%;
  cursor: pointer;
}

.slider-1:checked ~ .slider-controls__wrapper .slider-controls__item--1::before,
.slider-2:checked ~ .slider-controls__wrapper .slider-controls__item--2::before,
.slider-3:checked ~ .slider-controls__wrapper .slider-controls__item--3::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 12px;
  height: 12px;
  background-color: white;
  border: 3px solid #000000;
  border-radius: 50%;
}

.review__slider-buttons {
  display: none;
}

@media (min-width: @tablet-width) {
  .review__wrapper {
    width: 620px;
    padding-bottom: 0;
  }

  .review-slider {
    .list-style-none;
    display: flex;
    flex-direction: column;
    align-items: center;

    margin-top: 50px;
  }

  .review-slider__item {
    flex-direction: column-reverse;
    width: 620px;
  }

  .review-slider__item-name {
    order: 1;

    margin: 0 0 3px 0;
    padding: 0;

    font-size: 20px;
  }

  .review-slider__item-info {
    display: inline-flex;

    margin: 0;
    padding: 0;
    margin-bottom: 25px;

    color: @gray-blue;

    font-size: 14px;
    line-height: 30px;
    text-align: center;
  }

  .review-slider__item-text {
    width: 620px;
    order: 1;

    font-size: 18px;
    margin-bottom: 35px;
  }

  .review__slider-controls {
    margin-bottom: 80px;
  }
}

@media (min-width: @desktop-width) {
  .review__wrapper {
    position: relative;

    min-width: 940px;


    background-image: url("../img/icon-quotes.svg");
    background-repeat: no-repeat;
    background-position: center top 75px;
  }

  .review-slider {
    width: 860px;
    margin: 185px 0 0;
    padding-bottom: 0;
  }

  .review__slider-radio.radio {
    display: none;
  }

  .review-slider__item {
    width: 700px;
  }

  .review-slider__item-name {
    font-size: 24px;
  }

  .review-slider__item-text {
    width: 705px;
  }

  .review-slider__item-info {
    font-size: 18px;
  }

  .review__slider-controls {
    display: none;
  }

  .review__slider-buttons {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-470px, -50%);
    width: 940px;
  }

  .review__button-prev {
    position: absolute;
    left: 0;

    margin: 0;
    padding: 0;

    font-size: 0;
    border: none;
    cursor: pointer;
    background-color: transparent;

    &:hover path {
      fill: #7e868f;
    }

    &:active path {
      fill: #e9ebec;
    }
  }

  .review__button-next {
    position: absolute;
    right: 0;

    margin: 0;
    padding: 0;

    font-size: 0;
    border: none;
    background-color: transparent;

    svg {
      transform: rotate(180deg);
    }

    &:hover path {
      fill: #7e868f;
    }

    &:active path {
      fill: #e9ebec;
    }
  }
}
